


















































































import { getArticleInfo } from '@/utils/apis';
import Vue from 'vue';


export default Vue.extend({
    name: 'News',
    data() {
        return {
            hotList: [],
            newsList: [],
            total: 0,
            queryParam: {
                pageNum: 1,
                pageSize: 20
            }
        }
    },
    computed: {
        type() {
            return this.$route.params.type || 'default'
        }
    },
    watch: {
        type() {
            this.getNewsList()
        }
    },
    mounted() {
        if (this.type !== 'product') {
            this.getHotList()
        }
        this.getNewsList()
    },
    methods: {
        async getHotList() {
            const res = await getArticleInfo({
                pageNum: 1,
                pageSize: 3,
                type: 'default'
            })
            this.hotList = res.list
        },
        async getNewsList() {
            const res = await getArticleInfo({
                ...this.queryParam,
                type: this.type
            })
            this.newsList = res.list
            this.total = res.total
        }
    }
});
